// ------------------------ React
import React, { useState }  from 'react';


// ------------------------ Components
import QnaModal from '../components/QnaModal.js';


// ------------------------  
import styled from 'styled-components';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronRight } from '@fortawesome/free-solid-svg-icons';

const SnsWrap = styled.div `
  width: 1084px;
  margin: 0 auto 50px auto;
  text-align: center;
  .sns_span {
    color: #FC4C02;
    font-weight: bold;
  }
  .sns_zone {
    display: flex;
    justify-content: space-between;
    width: 200px;
    margin: 20px auto 0 auto;
    div {
      width: 42.5px;
      cursor: pointer;
    }
  }
`
const FooterTitleBox = styled.div `
  height: 50px;
  border-bottom: 1px solid #999;
  margin-bottom: 10px;
  span {
    font-size: 32px;
  }
  .footer_title {
    font-family: "Jua", sans-serif;
    margin-right: 10px;
  }
`

const FooterNav = styled.div `
  display: flex;
  justify-content: space-between;
  > div {
    width: calc(50% - 5px);
    .cs_center {
      padding-top: 10px;
      margin-bottom: 45px;
      p {
        font-size: 14px;
        line-height: 10px;
        color: #666;
      }
    } // cs_center

    .footer_btn_box {
      display: flex;
      justify-content: space-between;
      button {
        display: block;
        width: calc(50% - 5px);
        height: 50px;
        letter-spacing: -1px;
        border: 1px solid #ccc;
        border-radius: 10px;
        background-color: #fafaf8;
        box-sizing: border-box;
        &:hover {
          color: #FC4C02;
        }
      }
    } // footer_btn_box 
  }
  .footer_bored_box {
    .footer_bored > div {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #eee;
      padding: 10px 0;
      span {
        font-size: 14px;
        color: #666;
      }
      &:hover {
        font-weight: bold;
        cursor: pointer;
      }
    }
  } // footer_bored_box
`

const FooterOuter = styled.div `
  width: 100%;
  background-color: #eee;
  padding: 50px 0;
  span {
    font-size: 14px;
    color: #666;
  }
  .footer_info {
    margin-bottom: 20px;
  }
  .footer_info_btn {
  &:hover {
    text-decoration: underline 1px;
    text-underline-offset: 3px;
    cursor: pointer;
  }
  }
`


export default function Footer() {

  // qna_modal
  const [ openQna, setOpenQna ] = useState(false);

  return (
    <div>
      <footer>
        <section style={{width: '1084px', margin: '0 auto 50px auto'}}>
          <SnsWrap>
            <div className='sns_text'>
              <span> SNS  </span>
              <span className='sns_span'></span>
              <span>  !</span>
            </div>

            <div className='sns_zone'>
              <div><img src={process.env.PUBLIC_URL + '/images/sns_01.png'} /></div>
              <div><img src={process.env.PUBLIC_URL + '/images/sns_02.png'} /></div>
              <div><img src={process.env.PUBLIC_URL + '/images/sns_03.png'} /></div>
              <div><img src={process.env.PUBLIC_URL + '/images/sns_04.png'} /></div>
            </div>
          </SnsWrap>

          <FooterNav>
            <div>
              <FooterTitleBox>
                <span className='footer_title'></span>
                <span>010-4110-2175</span>
              </FooterTitleBox>
              <div className='cs_center'>
                <p>:  09:00 ~ 18:00 ( 13:00 ~ 14:00)</p>
                <p> sara2175@naver.com</p>
                <p> 00-0000-0000</p>
              </div>
              <div className='footer_btn_box'>
                <button
                  onClick={() => setOpenQna(true)}
                >
                  1:1
                  <FontAwesomeIcon icon={faChevronRight} />
                </button>
                <button>
                   / 
                  <FontAwesomeIcon icon={faChevronRight} />
                </button>
              </div>
            </div>

            <div className='footer_bored_box'>
              <FooterTitleBox>
                <span className='footer_title'></span>
              </FooterTitleBox>
              <div className='footer_bored'>
                <div>
                  <span>[EVENT]  Light  EVENT</span>
                  <span>2025/07/31</span>
                </div>
              </div>
              <div className='footer_bored'>
                <div>
                  <span> 100%    </span>
                  <span>2025/07/13</span>
                </div>
              </div>
              <div className='footer_bored'>
                <div>
                  <span>    ()</span>
                  <span>2025/05/31</span>
                </div>
              </div>
              <div className='footer_bored'>
                <div>
                  <span> X    </span>
                  <span>2025/05/01</span>
                </div>
              </div>
            </div>

          </FooterNav>
        </section>

        <FooterOuter>
          <div style={{width: '1048px', margin: '0 auto'}}>
            <div className='footer_info'>
              <span> | </span>
              <span>  | </span>
              <span>   688  2 | </span>
              <span> 000-00-00000 </span>
              <span className='footer_info_btn'>(  )</span>
            </div>
            <div className='footer_info'>
              <span>     ,       .</span>
              <br />
              <span>: /,  /  </span>
              <span className='footer_info_btn'>(   )</span>
            </div>
            <div>
              <span>COPYRIGHT  2025 Benope Corp. All rights reserved.</span>
              <br />
              <span style={{fontWeight: 'bold', color: '#999'}}>     .</span>
            </div>
          </div>
        </FooterOuter>
      </footer>

      <QnaModal
        open={openQna}
        // open  openQna true 
        close={() => setOpenQna(false)}
        // setOpenQna   false   
      />

    </div>
  )
}
